.container{
  display: block;
  max-width: 480px;
  margin: 0 auto;
  padding: 1rem;
}
.main{
 background-color: #fff;
 box-shadow:  0 1px 2px rgba(0,0,0,0.25);
}
.title{
  color:#fab1a0;
  font-size: 64px;
  font-weight: 100;
  text-align: center;
}
#new-todo{
  background-color: rgba(0,0,0,0.03);
  box-shadow: inset 0 -2px 1px  rgba(0,0,0,0.03);
  font-size: 1.5rem;
  font-style:italic;
  width: 100%;
  padding:  16px 16px 18px 16px;
}
#button{
  width: 100%;
  border: none;
  height: 100%;
  color :rgba(0,0,0,0.2);
  transition:  opacity 0.3s ease;
  box-shadow: inset 0 -2px 1px  rgba(0,0,0,0.03);
}
#button:hover{
 opacity: 0.8;
 color: white;
 background-color: rgb(6, 216, 6);
 cursor: pointer;
}

ul.todolist li{
  display: flex;
  border-top: 1px solid #dfe6e9;
  border-bottom: 1px solid #dfe6e9;
}

.todo{
  flex:1;
  outline: none;
  cursor: pointer;
  font-size: 1.5rem;
  word-break:break-all;
  line-height: 1.2;
  transition: color 0.3s;
  padding: 15px 15px 17px 15px;
}
#button2{
  border: none;
  background-color: transparent;
  color: gray;
  transition: opacity 0.3s ease;
}

 #button2:hover{
  background-color: red;
  color: white;
  cursor: pointer;
 }

 .todo.checked{
  color: #b2bec3;
  text-decoration: line-through;
 }
